@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@300&family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
html,
body {
    min-height: 100vh;
}

body {
    margin: 0;
}

#root {
    min-height: 100vh;
}

.PageContent {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    position: relative;
}

.DownButtonCont {
    position: fixed;
    z-index: 6;
    max-height: 7vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 7.5px;
    bottom: 0;
    -webkit-transform: rotate(90deg) scaleX(1);
            transform: rotate(90deg) scaleX(1);
}

.UpButtonCont {
    position: fixed;
    z-index: 6;
    max-height: 7vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 7.5px;
    top: 0;
    -webkit-transform: rotate(90deg) scaleX(1);
            transform: rotate(90deg) scaleX(1);
}

.ChangeButton {
    font-size: 100px;
    background-color: rgba(0, 0, 0, 0.6);
    color: #fff;
    font-family: Staatliches;
    height: 40vh;
    border: none;
    border-radius: 5px;
}

.ChangeButton:hover {
    background-color: rgba(0, 0, 0, 0.9);
}

.ButtonText {
    color: white;
    opacity: 1;
}

.AboutMeParentCont {
    min-width: 100%;
    height: 0vh;
    position: fixed;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0.4);
    overflow: hidden;
    display: flex;
    align-items: center;
}

.AboutMeParentCont.active.fromHome {
    height: 100vh;
    -webkit-animation-name: showCont;
            animation-name: showCont;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    bottom: 0;
    top: unset;
}

.AboutMeParentCont.active.fromProjects {
    height: 100vh;
    -webkit-animation-name: showCont;
            animation-name: showCont;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    bottom: unset;
    top: 0;
}

.AboutMeParentCont.unactive.home {
    height: 0vh;
    -webkit-animation-name: hideAbout;
            animation-name: hideAbout;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    top: unset;
    bottom: 0;
}

.AboutMeParentCont.unactive.projects {
    height: 0vh;
    -webkit-animation-name: hideAbout;
            animation-name: hideAbout;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    top: 0;
    bottom: unset;
}

@-webkit-keyframes showCont {
    from {
        height: 0vh;
    }
    to {
        height: 100vh;
    }
}

@keyframes showCont {
    from {
        height: 0vh;
    }
    to {
        height: 100vh;
    }
}

@-webkit-keyframes hideAbout {
    from {
        height: 100vh;
    }
    to {
        height: 0vh;
    }
}

@keyframes hideAbout {
    from {
        height: 100vh;
    }
    to {
        height: 0vh;
    }
}

.AboutMeContentParnetCont {
    min-height: 90%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
    color: white;
    margin-bottom: 1%;
}

.PhotoFavsCont {
    max-width: 35%;
    min-width: 35%;
    min-height: 80%;
    padding: 1%;
    /* background-color: rgba(0, 0, 0, 0.5); */
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.AboutMeTitle {
    font-size: 124px;
    font-family: Staatliches;
    max-height: 130px;
}

.AboutMeTextPictCont{
    display: flex;
    width: 95%;
    justify-content: space-around;
    align-items: center;
}

.AboutMeText {
    font-size: 40px;
    font-family: Staatliches;
    text-align: center;
    max-width: 60%;
    min-width: 60%;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 25px;
    padding: 5px;
    min-height: 50vh;
}

.SelfImage {
    border-radius: 50vh;
    width: 540px;
    height: 527.25px;
    border: 3px solid black;
}

.FavoriteCont {
    min-width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.FavoriteTitle {
    font-size: 65px;
    font-family: Staatliches;
    text-decoration: underline;
    margin-bottom: 0.5vh;
}

.FavoriteUL {
    font-size: 45px;
    font-family: Staatliches;
    margin-top: unset;
    margin-bottom: unset;
    list-style-type: square;
    display: flex;
    min-width: 76%;
    justify-content: space-between;
    height: 0vh;
    overflow: hidden;
}

.HomeContentCont {
    min-width: 100%;
    min-height: 100vh;
    max-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 1;
    overflow: hidden;
}

.HomeContentCont.showCont {
    max-height: 100vh;
    min-height: 100vh;
    -webkit-animation-name: showCont;
            animation-name: showCont;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.HomeContentCont.hideCont {
    -webkit-animation-name: hideCont;
            animation-name: hideCont;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    max-height: 0vh;
    min-height: 0vh;
}

@-webkit-keyframes showCont {
    from {
        max-height: 0vh;
        min-height: 0vh;
    }
    to {
        max-height: 100vh;
        min-height: 100vh;
    }
}

@keyframes showCont {
    from {
        max-height: 0vh;
        min-height: 0vh;
    }
    to {
        max-height: 100vh;
        min-height: 100vh;
    }
}

@-webkit-keyframes hideCont {
    from {
        max-height: 100vh;
        min-height: 100vh;
    }
    to {
        max-height: 0vh;
        min-height: 0vh;
    }
}

@keyframes hideCont {
    from {
        max-height: 100vh;
        min-height: 100vh;
    }
    to {
        max-height: 0vh;
        min-height: 0vh;
    }
}

.HomeCanvas {
    position: fixed;
    z-index: 0;
    top: 0;
    left: 0;
    overflow: hidden;
    background-color: black;
    min-height: 100vh;
    min-width: 100%;
}

.HomeInfoCont {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-width: 100%;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1;
}

.HomeName {
    font-size: 148px;
    font-family: Staatliches;
    color: white;
    border-bottom: 15px solid white;
    margin-bottom: 2%;
}

.HomeLinkCont {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 1.3%;
}

.HomeLink {
    font-size: 100px;
    font-family: Staatliches;
    cursor: pointer;
    /* text-decoration: underline; */
    color: white;
}

.HomeLink:hover {
    text-decoration: underline;
    color: #62bcb9;
}



.ProjectsParentCont {
    min-width: 100%;
    height: 0vh;
    position: fixed;
    bottom: 0;
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.8);
}

.ProjectsParentCont.active {
    height: 100vh;
    -webkit-animation-name: raise;
            animation-name: raise;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.ProjectsParentCont.unactive {
    height: 0vh;
    -webkit-animation-name: lower;
            animation-name: lower;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

@-webkit-keyframes raise {
    from {
        height: 0vh;
    }
    to {
        height: 100vh;
    }
}

@keyframes raise {
    from {
        height: 0vh;
    }
    to {
        height: 100vh;
    }
}

@-webkit-keyframes lower {
    from {
        height: 100vh;
    }
    to {
        height: 0vh;
    }
}

@keyframes lower {
    from {
        height: 100vh;
    }
    to {
        height: 0vh;
    }
}

